.day-schedule{
    width: 165px;
    /*border: 1px solid black;*/
    background-color: #fff;
    padding: 6px;
    margin-top: 6px;
}
.meal-heading{
    font-size: 1.1rem;
}
.meal{
    padding-top: 5px;
    padding-left: 5px;
    padding-right: 5px;
    /*font-size: 0.9rem;*/
    background-color: silver;
    display: flex;
    justify-content: space-between;
    font-weight: normal;
    
}
.add-menu{
    /*border: 1px solid black;
    border-radius: 50%;*/
    text-decoration: none;
    font-size: large;
    color: black;
}
.date-bar{
   padding: 0 0 12px 6px;
   /*text-align: left;*/
}
.pref-date-bar{
    padding-right: 20px;
    font-size: 1.6rem;
}
.pref-date-bar-input{
    /*padding: 5px 20px 12px 5px;*/
    font-size: 1.2rem;
}
.input-cw{
    width: 40px;
}
.input-year{
    width: 60px;
}
main{
    width: 70%;
    margin: 0 auto;
    padding-top: 5%;
}
.diary{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}


@media (max-width: 1366px){
    main{
        width: 100%;
        /*background-color: sienna;*/
    }
}

.menu{
    display: flex;
    justify-content: center;
    margin-top: 5%;
}
.menu-heading{
    font-size: 1.2rem;
}
.menu-for{
    font-size: 1rem;
}
.meal-menu{
    padding: 0;
    font-weight: bold;
}
.day-date{
    font-size: 1rem;
}
.menu-form{
    display: flex;
}
input{
    font-size: 1.2rem;
}
.back-to-menu{
    text-decoration: none;
    color: black;
}
